<template>
        <div class="main">
            <div class="main-center">
                <p>Begin Anywhere</p>
                <h6>Your passion will guide you</h6>
                <el-button type="info">View our works</el-button>
            </div>
            <el-row class="main-bottom">
                <el-button v-for="(button, index) in buttons" :key="index" :type="button.type" :icon="button.icon" circle></el-button>
            </el-row>
        </div>
</template>

<script>
export default {
  name: 'iMain',
  data () {
    return {
      buttons: [
          {
            icon: "el-icon-search",
            type: "",
            url: "#"
          },
          {
            icon: "el-icon-edit",
            type: "primary",
            url: "#"
          },
          {
            icon: "el-icon-check",
            type: "success",
            url: "#"
          },
          {
            icon: "el-icon-message",
            type: "info",
            url: "#"
          },
          {
            icon: "el-icon-star-off",
            type: "warning",
            url: "#"
          }
      ]
    }
  }
}
</script>

<style scoped>
  
/* main——start */
.main {
  width: 100%;
}
.main-center {
  margin: 0 auto;
  padding: 50px;
  text-align: center;
  color: white;
}
.main-center p{
  font-size: 55px;
}
.main-center h6{
  font-size: 20px;
}
.main-center button{
  margin: 26px;
}
.main-center p, h6, button {
  margin: 20px;
}

.main-bottom {
  width: 50%;
  margin: 0 auto;
  text-align: center;
  color: white;
}
.main-bottom button {
  margin: 0 30px 0 30px ;
}
/* main——end */
</style>


