import Vue from 'vue'
import Router from 'vue-router'
/* 组件 */
import Main from '@/components/Main'
import Message from '@/components/Message'
import Notes from '@/components/Notes'
import Share from '@/components/Share'

Vue.use(Router)

export default new Router({
  routes: [
    {
      /* 默认页面 */
      meta:{
        index:1
      },
      path: '/', 
      component: Main
    },
    {
      /* 首页 */
      meta:{
        index:1
      },
      path: '/main', 
      component: Main
    },
    {
      /* 首页 */
      meta:{
        index:2
      },
      path: '/share', 
      component: Share
    },
    {
      /* 笔记 */
      meta:{
        index:3
      },
      path: '/notes', 
      component: Notes
    },
    {
      /* 留言板 */
      meta:{
        index:4
      },
      path: '/message', 
      component: Message
    }
  ]
});




