<template>
        <div class="notes" v-loading="loading">
            <h5>notes</h5>
            <el-row :gutter="20" justify="space-around">
              <el-col :span="8" v-for="(note, index) in notes" :key="index">
                <div class="notes-item">
                  <img width="100%" :src="note.img" />
                  <h2>
                    {{note.title}}
                  </h2>
                  <p>
                    {{note.content}}
                  </p>
                  <a :href="note.url">Read More</a>
                </div>
              </el-col>
            </el-row>
        </div>
</template>

<script>
export default {
  name: 'iMain',
  data () {
    return {
      notes: null,
      loading: true
    }
  },
  mounted () {
    var self = this;
    this.$http.get('../../static/data/notes.json')
      .then(function (response) {
        self.notes = response.data.notes;
        self.loading = false;
      })
      .catch(function (error) {
        self.$message('数据加载异常');
        self.loading = false;
      })
  }
}
</script>

<style scoped>
  
/* notes——start */
.notes {
  width: 100%;
  background: RGBA(250, 250, 250, 0.5);
}
.notes h5 {
  color: #333;
  font-size: 30px;
  line-height: 1.3em;
  font-weight: 600;
  text-align: center;
}

.notes-item {
  background: #fff;
  padding-bottom: 20px;
  margin-bottom: 16px;
}
.notes-item h2, .notes-item p, .notes-item a {
  width: 80%;
  margin-left: 10%;
  text-align: left;
  word-wrap:break-word;
  word-break:break-all;
  overflow: hidden;
}
.notes-item h2 {
  height: 26px;
}
.notes-item p, .notes-item a {
  height: 70px;
}
.notes-item h2 {
  font-size: 10px;
  color: #2b2b2b;
  font-size: 18px;
  line-height: 1.3em;
  font-weight: 600
}
.notes-item p {
  font-size: 13px;
  color: #909090;
  line-height: 1.7em;
}
.notes-item a {
  line-height: 20px;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 600;
}


/* notes——end */
</style>


