<template>
  <div id="app">
    <!-- 头部搜索 -->
    <i-header></i-header>
    <!-- 头部搜索 -->
    <main>
      <transition :name="transitionName">
        <router-view style="position:absolute;"></router-view>
      </transition>
    </main>
  </div>
</template>

<script>
  import iHeader from './components/Header'

  export default {
    name: 'App',
    data() {
      return {
        transitionName: '',
        isScroll: false
      }
    },
    components : {
      iHeader
    },
    watch: {
      /* 监听router-link, 进行动画效果 */
      $route(to, from) {
          this.transitionName = (to.meta.index > from.meta.index) ? 'slide-left' : 'slide-right' ;
      }
    },
  }
</script>

<style>

  /* scrollar——start */
  ::-webkit-scrollbar, ::-webkit-scrollbar-thumb , ::-webkit-scrollbar-corner {
    display: none;
  }
  body {
    background: url("./assets/bg.jpg") no-repeat center center;
    background-size: cover;
    background-attachment: fixed;
  }
  #app {
    width: 1200px;
    min-height: 600px;
    margin: 0 auto;
  }

  /* scrollar——end */

  /* main——start */
  main {
    width: 85%;
    margin: 0 auto;
    position: relative;
  }
  /* main——end */

  /*router_transition-start*/
  .slide-right-enter-active,
  .slide-right-leave-active,
  .slide-left-enter-active,
  .slide-left-leave-active {
    will-change: transform;
    transition: all 1100ms;
  }
  .slide-right-enter {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
  .slide-right-leave-active {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
  .slide-left-enter {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
  .slide-left-leave-active {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
  
  /*router_transition-end*/
</style>



