<template>
        <header>
            <div class="header-user">
              <img class="header-logo" src="../assets/user.jpg" />
              <p>Mr Lin</p>
              <span>“一个热爱技术，喜欢钻研的猫咪饲养员~”</span>
            </div>
            <el-row class="header-right">
                <router-link v-for="(item, index) in items" :key="index" :to="item.url">
                  <el-col :span="6" >{{item.name}}</el-col>
                </router-link>
            </el-row>
        </header>
</template>

<script>
export default {
  name: 'iHeader',
  data () {
    return {
      items: [
          {
            name: "主页",
            url: "/main"
          },
          {
            name: "分享",
            url: "share"
          },
          {
            name: "笔记",
            url: "/notes"
          },
          {
            name: "留言板",
            url: "/message"
          }
      ],
    }
  }
}
</script>

<style scoped>
  /* header——start */
  header {
    width: 100%;
    height:70px;
    padding-top: 30px;
  }
  .header-right , .header-logo{
    height: 50px;
    line-height: 50px;
  }
  .header-right {
    margin-top: 20px;
  }
  .header-user {
    float: left;
  }
  .header-user p,.header-user span {
    line-height: 70px;
    margin-left: 10px;
    display: inline;
  }
  .header-user p {
    color: #663636;
  }
  .header-user span {
    color: #706b6b;
  }

  .header-logo {
    border-radius: 50%;
    margin-left: 50px;
  }
  .header-right {
    width: 45%;
    text-align: center;
    font-size: 13px;
    float: right;
  }
  .header-right .el-col {
    border-radius: 0;
  }
  .header-right a{
    text-decoration: none;
    color: white;
    font-size: 16px;
  }
  .header-right .el-col:hover{
    border-bottom: 2px solid RGB(243, 243, 250, 0.5);
  }
  /* header——end */

</style>


